.app {
  background-image: linear-gradient(128deg, #140f16 0%, #212b1e 100%);
  min-height: 100vh;
  background-repeat: repeat;
}

.landing {
  color: #b1ecff;
  text-shadow: 2px 2px #7A5B51;
  -webkit-text-stroke-width: 0.5px;
  -webkit-text-stroke-color: #a3dbeeb4;
  text-align: center;
  font-size: calc(10px + 2vmin);
  font-family: 'Roboto', sans-serif;
  justify-content: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.landing h1 {
  font-family: 'Philosopher', sans-serif;
}

.landing-name-pic {
  display: flex;
  align-items: center;
  justify-content: center;
}

.portrait {
  max-height: 23vh;
  min-height: 15vh;
  height: 25vw;
  margin-left: 15px;
}

button {
  cursor: pointer;
}

.button {
  background-color: #29211f;
  color: rgb(243, 164, 118) !important;
  text-shadow: 0px 0px #7a5b5100;
  -webkit-text-stroke-width: 0.5px;
  -webkit-text-stroke-color: #ad1d1d85;
  font-family: 'Roboto', sans-serif;
  border: #5B7A51 1px solid;
  border-radius: 5px;
  font-size: calc(5px + 2vmin);
  margin: 1vw;
  padding: 5px;
  text-decoration: none;
}

.button:hover {
  background-color: #201a19;
  color: #b1ecff !important;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #a3dbeeb4;
  font-family: 'Roboto', sans-serif;
  border: #97c289 1px solid;
  border-radius: 4px;
  color: rgb(250, 81, 2);
}

.about {
  color: #b1ecff;
  -webkit-text-stroke-width: 0.5px;
  -webkit-text-stroke-color: #a3dbeeb4;
  text-align: center;
  font-size: calc(7px + 1vmin);
  font-family: 'Roboto', sans-serif;
}

.about-q {
  background-color: rgba(0, 0, 0, 0.233);
  border: #97c289 1px solid;
  border-radius: 3px;
  margin: 3vw;
  padding: 1vw;
  min-width: 20vw;
}

.about-certs {
  background-color: rgba(0, 0, 0, 0.39);
  border: #000000 1px solid;
  border-radius: 3px;
  margin-top: 0;
  min-width: 100vw;
}

.about-name-pic {
  display: flex;
  align-items: center;
  justify-content: center;
}

.about-duo {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 3vw;
}

.about-duo-certs {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

#certs {
  display: flex;
  justify-content: center;
  text-align: center;
}

#about-pictures {
  padding: 0 5vw 0 5vw;
  margin: 0 5vw 0 5vw;
}

.projects-group {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  color: #b1ecff;
  -webkit-text-stroke-width: 0.5px;
  -webkit-text-stroke-color: #a3dbeeb4;
}

.project-card {
  text-align: center;
  flex: 1;
  background-color: rgba(177, 235, 255, 0.068);
  border: #7ca36e 1px solid;
  border-radius: 3px;
  max-width: 27vw;
  margin: 3vh;
  min-height: 50vh;
}

.project-card img {
  max-width: 20vw;
  border: #7ca36e 1px solid;
  border-radius: 3px;
}

header {
  display: flex;
  justify-content: space-between;
}

header h1 {
  font-family: 'Philosopher', sans-serif;
  color: #b1ecff;
  text-shadow: 2px 2px #7A5B51;
  -webkit-text-stroke-width: 0.5px;
  -webkit-text-stroke-color: #a3dbeeb4;
  font-size: calc(10px + 4vmin);
  margin-left: 5vw;
}

.header-nav {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 5vw;
}

.contactForm {
  display: flex;
  justify-content: center;
  text-align: center;
  color: #b1ecff;
  -webkit-text-stroke-width: 0.5px;
  -webkit-text-stroke-color: #a3dbeeb4;
  padding: 15px;
  background-color: rgba(0, 0, 0, 0);
}

.contactFormBorder {
  border: #7ca36e 1px solid;
  border-radius: 3px;
  background-color: rgba(0, 0, 0, 0.39);
  padding: 16px;
}

form input, form textarea {
  min-width: 66vw;
  margin-bottom: 20px !important;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.1), rgb(255, 255, 255), rgba(0, 0, 0, 0.1));
}

form textarea {
  height: 150px;
}

footer {
  display: flex;
  justify-content: center;
}

.cert-link {
  text-decoration: none;
  color: rgb(218, 110, 49);
}

.cert-link:hover {
  border: #7ca36e 1px solid;
  border-radius: 3px;
  color: rgb(149, 213, 255);
}

.cert-field {
  color: rgb(176, 201, 218);
}

.cert-field:hover {
  color: rgb(218, 110, 49);
}

.border-y {
  border: #97c289 1px solid;
  border-radius: 3px;
}

.project-description {
  padding: 1vw;
}

.project-stack {
  padding: 1vw;
  color: #d065fa;
  -webkit-text-stroke-width: 0.5px;
  -webkit-text-stroke-color: #ff67f77e;
  text-shadow: 0px 0px #7a5b5100;
}

#thinkful {
  font-size: larger;
}

#thinkful:hover {
  color: white;
}

@media all and (max-width: 768px) {
  .landing-name-pic {
    flex-direction: column;
  }
  .portrait {
    height: 66vw;
    margin-left: 15px;
    margin-top: 3vh;
  }
  .projects-group {
    flex-direction: column;
    margin: 0 7vw 0 7vw;
  }
  .project-card {
    max-width: 90vw;
  }
  .project-card img {
    min-width: 60vw;
  }
  .cert-group {
    display: flex !important;
    flex-direction: column !important;
    justify-content: space-evenly !important;
  }
}

@media all and (max-width: 1080px) {
  .about-duo {
    flex-direction: column;
  }
  .about-duo .portrait {
    height: 33vw;
  }
  .cert-group {
    padding: 1vh;
  }
  .cert-group div {
    padding: 1vh;
  }
}